export default HireContentMarketer;
export const queryData = graphql`
  query HireContentMarketerPageQuery {
    HireInfluencerMarketingSpecialistImg: file(
      relativePath: { eq: "hire-content-marketer.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: { eq: "hirescreens/hire-content-marketer/hero.png" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }
    experienceImage: file(
      relativePath: { eq: "hirescreens/hire-content-marketer/experience.png" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }
    benefitsImage: file(
      relativePath: { eq: "hirescreens/hire-content-marketer/benifits.png" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-content-marketer" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
